import React, { useState} from 'react';
import ColorTheme from "./ColorTheme.js";

function Header_Minimized({connect, disconnect, connectionStatus, selectBoard, expand}) {
    let height = 70;
    let connectButton = <button type="button" className="btn color1 btn-block" style={{height:"100%", fontWeight:"bold",fontSize:"1.25em"}} onClick={() => {connect()}} > Connect </button> ;
    
    if(connectionStatus)
    {
        connectButton = <button type="button" className="btn color1 btn-block" style={{ height:"100%", fontWeight:"bold", fontSize:"1.25em"}} onClick={() => {disconnect()}} disabled={!connectionStatus}> Disconnect </button> ;
    }
    return (
        <>
            <div className="container-fluid p-0" style={{fontWeight:"bold"}}>
                <div className="row no-gutters color1">
                    <div className="col-12 col-md-2 pt-3" style={{height: height, borderRight:"solid 3px var(--fontColor1)"}} >
                        <div class="row justify-content-center no-gutters">
                            <div class="col-5">
                                {connectButton}
                            </div>
                            <div class="col-6 ml-1">
                                <div class="btn-group w-100" role="group" aria-label="Select Board Type"  style={{height:"100%"}} >
                                    <button class="btn color1 dropdown-toggle" style={{ fontWeight:"bold",fontSize:"1.25em"}} type="button" id="portBoard" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        Select Board
                                    </button>
                                    <div class="dropdown-menu" id="portBoard_group" aria-labelledby="portBoard">
                                        <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('PlaygroundClassic')}}>Adafruit Circuit Playground Classic</a>
                                        <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('PlaygroundExpress')}}>Adafruit Circuit Playground Express</a>
                                        <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('Uno')}}>Arduino Uno</a>
                                        <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('BBCMicroBit')}}>BBC micro:bit</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-12 col-md-8 pt-3 " style={{height: height, borderRight:"solid 3px var(--fontColor1)"}}>
                        <a onClick={() => expand(true)} style={{cursor:"pointer"}}>
                            <div class="row no-gutters justify-content-center">
                                <div class="col-2">
                                    <svg xmlns="http://www.w3.org/2000/svg" class="float-right" viewBox="0 0 16 16" width="18%" ><path fill="var(--fontColor1)" fill-rule="evenodd" d="M12.78 6.22a.75.75 0 010 1.06l-4.25 4.25a.75.75 0 01-1.06 0L3.22 7.28a.75.75 0 011.06-1.06L8 9.94l3.72-3.72a.75.75 0 011.06 0z"></path></svg>
                                </div>
                                <div class="col-4">
                                    <p class="h3 text-center">Expand to View Instructions</p>
                                </div>
                                <div class="col-2">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" width="18%" ><path fill="var(--fontColor1)" fill-rule="evenodd" d="M12.78 6.22a.75.75 0 010 1.06l-4.25 4.25a.75.75 0 01-1.06 0L3.22 7.28a.75.75 0 011.06-1.06L8 9.94l3.72-3.72a.75.75 0 011.06 0z"></path></svg>
                                </div>
                            </div>
                        </a>
                    </div>
                    <div className="col-12 col-md-2 pt-3" style={{height: height}} >
                        <div class="row justify-content-center no-gutters">
                            <div class="col-8">
                                <ColorTheme width="100%" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
  
export default Header_Minimized;