import React, { useState, useEffect } from 'react';
import './index.css';
import MicroView from "./Debugger_Components/Layouts/MicroView.js";
import Header from "./Debugger_Components/Layouts/Header.js";
var Tone = require('tone');
//const daemon = new Daemon('https://schneidercode.github.io/Arduino-Live-Web-Debugger/index.html');

function App(props){
  const [board, setBoard] = useState("PlaygroundClassic");
  //Handle Pin data
  const [pinData, setPinData] = useState({"D0":[0,0],"D1":[0,0],"D2":[0,0],"D3":[0,0],"D4":[0,0],"D5":[0,1],"D6":[0,0],"D7":[2,0],"D8":[1,1],"D9":[0,0],"D10":[0,0],"D11":[0,0],"D12":[0,0],"D13":[0,1],"D14":[2,0],"D15":[0,1],"D16":[2,1],"D17":[0,1],"D18":[2,0],"D19":[0,0],"D20":[0,0],"D21":[0,0],"D22":[0,0],"D23":[0,0],"D24":[0,0],"D25":[0,0],"D26":[1,1],"D27":[0,0],"D28":[0,0],"D29":[0,0],"D30":[0,1],"A0":531,"A1":0,"A2":35,"A3":1023,"A4":338,"A5":26,"A6":0,"A7":0,"A8":1023,"A9":426,"A10":0,"A11":0});
  const[deviceData, setDevices] =  useState({ pwm : {}, motion: {X: 0.22, Y: 0.34, Z: 9.1}, sound: 57.56 });

  //Data Ping Interval
  const [intervalId, setIntervalId] = useState(null);

  //Data Ping Interval amount
  //TODO: Create dropdowns to set these intervals
  let intervalUpdate = 50;
  const synth = new Tone.Synth().toDestination();
  /**
   * This method handles most of the Live Debugger's communication with the Serial Device. The selection parameter determines what type 
   * of information is being requested from or passed to the Serial Device. The data parameter is an optional object that holds any values needed
   * for the message being sent.
   * 
   * @param {string} selection 
   * @param {object} data 
   */
  function queryBoard(selection, data)
  {
    let copy = null;
    let id = null;
    try{

      //Don't call for new data until all existing data has been received
      switch(selection)
      {
        case 'PINS'://Set a reocurring interval to ping for current pin states
          if(intervalId !== null)
          {
              clearInterval(intervalId);
          }
          setIntervalId(null);
        break;
          
        case 'SOUND'://Set a reoccuring interval to ping for current sound levels (decibels)
          if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            id = setInterval(() =>
            {
              let sound = {sound: parseFloat((deviceData.sound + (Math.random()*4)-2).toFixed(2))};
              setDevices(deviceData => { return {...deviceData, ...sound }});
            }, intervalUpdate);
            setIntervalId(id);
          break;

        

        case 'MOTION'://Set a reoccuring interval to ping for current accelerometer (x,y,z) data
          if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            id = setInterval(() =>
            {
              console.log("Motion");
              let motion = {motion: {X: (0.5 + (Math.random())-0.5).toFixed(2), Y: (1.2 + (Math.random())-0.5).toFixed(2), Z: (9.02 + (Math.random())-0.5).toFixed(2)}};
              setDevices(deviceData => { return {...deviceData, ...motion }});
            }, intervalUpdate);
            setIntervalId(id);
          break;

          case 'PWM': //Have microcontroller update a pin's PWM/AnalogWrite()
            copy = deviceData;
            copy.pwm[data.pin.toString()] = data.value;
            setDevices(deviceData => { return {...deviceData, ...copy }});
            break;
        
          case 'PINMODE': //Update a pin's mode
            copy = pinData;
            copy[data.pin.toString()][1] = data.value;
            if(data.value == 2)
            {
              copy[data.pin.toString()][0] = 1;
            }
            else 
            {
              copy[data.pin.toString()][0] = 0;
            }
            setPinData(pinData => { return {...pinData, ...copy }});
            break;
        
          case 'DIGITAL': //Update a pin's digital value
            copy = pinData;
            copy[data.pin.toString()][0] = data.value;
            setPinData(pinData => { return {...pinData, ...copy }});
            break;

          case 'TONE': //Have a speaker place a specific tone (frequency) for a set time interval
            if(intervalId !== null)
            {
                clearInterval(intervalId);
            }
            if(data.time)
            {
              synth.triggerAttackRelease(data.freq, data.time / 1000);
            }
            break;
      }
    }
    catch(e){
      console.log(e);
    }
  }/* End queryBoard(selection, data) */

  return(
    <>
      <Header connect={() =>alert("Connect Clicked (Demo)")} disconnect={() =>alert("Connect Clicked (Demo)")} connectionStatus={false} selectBoard={setBoard}/>
      <MicroView pinData={pinData} deviceData={deviceData} serialCom={queryBoard} board={board}/>
    </>
  );
}

export default App;