
import * as React from "react"
import PinCircle from "../PinControls/PinCircle.js"
import PlaygroundClassic from "./PlaygroundClassic.js"
import PinKey from "../PinControls/PinKey.js"
import HiddenPin from "../PinControls/HiddenPin.js"

/**
 * This component will display a UI for the Live Debugger to interact with an Adafruit Circuit Playground Classic
 * @param {*} param0 
 */
function PlaygroundClassic_Layout({pinData, trigger, disabled, deviceData}) {
  /***
   * 
   * 
   * This code block sets digitalValues -> an array that keeps track of a pin's current setting (Low: 0, High: 1, High-In: 2, PWM-ON: 3, Disabled: 4), 
   * this is passed to the  PinCircle component as a setting prop, which determines the pin's color.
   * This is all hardcoded for the the Circuit Playground Classic's pin/sensor ids.
   */
  //TODO Convert to use object/key pair and then use ids for the keys down below
  let digitalPins = ["D3", "D2", "D0", "D1", "D10", "D9", "D6", "D12"];
  let digitalValues = [0,0,0,0,0,0,0,0];
  
  //Preset Digital Interfaces
  let buttonRight =  <stop offset="0%" stop-color="#1E1E1E"></stop>;
  let buttonLeft =  <stop offset="0%" stop-color="#1E1E1E"></stop>;
  let bottomSwitch =  <stop offset="0%" stop-color="#333333"></stop>;
  
  //Handle onboard sensor data
  let temp = "NA";
  if(pinData !== null)
  {
    try{
        if(disabled)
        {
          digitalValues = [4,4,4,4,4,4,4,4];
        }
        else
        {
          if(pinData.hasOwnProperty("A0"))
          {
            temp = pinData["A0"];
          }

          if(pinData["D4"][0] === 2)
          {
            buttonLeft =  <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                          </stop>;
          }

          if(pinData["D19"][0] === 2)
          {
            buttonRight = <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                        </stop>;
          }

          if(pinData["D21"][0] === 2)
          {
            bottomSwitch = <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                        </stop>;
          }
          
          for(let i = 0; i < digitalPins.length; i++)
          {
            digitalValues[i] = pinData[digitalPins[i]][0];
          }

          if(deviceData.pwm["D3"] > 0 && deviceData.pwm["D3"] < 255)
          {
            digitalValues[0] = 3;
          }
          if(deviceData.pwm["D6"] > 0 && deviceData.pwm["D6"] < 255)
          {
            digitalValues[6] = 3;
          }
          if(deviceData.pwm["D9"] > 0 && deviceData.pwm["D9"] < 255)
          {
            digitalValues[5] = 3;
          }
          if(deviceData.pwm["D10"] > 0 && deviceData.pwm["D10"] < 255)
          {
            digitalValues[4] = 3;
          }
        }
      }
    catch(e)
    {
      console.log(e);
    }
  }
    return (
        <>
        {/* Display Chosen Board */}
          <PlaygroundClassic width={600} height={600} x={420.08} y={125} />
          
          <defs>  
            <linearGradient id="pinGradientHigh" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="red">
                    <animate attributeName="stop-color" values="white;red;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinGradientHighIn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="yellow">
                    <animate attributeName="stop-color" values="white;yellow;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinGradientPwmOn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="#66CCEE">
                    <animate attributeName="stop-color" values="white;#66CCEE;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinDisabled" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="black">
                    <animate attributeName="stop-color" values="black" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="buttonRight" x1="0%" y1="0%" x2="100%" y2="100%" > 
               {buttonRight} 
            </linearGradient>
            <linearGradient id="buttonLeft" x1="0%" y1="0%" x2="100%" y2="100%" > 
                {buttonLeft}
            </linearGradient>
            <linearGradient id="bottomSwitch" x1="0%" y1="0%" x2="100%" y2="100%" > 
                {bottomSwitch}
            </linearGradient>
          </defs>
          
          {/*Left Side */}
          <PinCircle analog="NA" digital="D3" pwm="D3" x={511.08} y={252} trigger={trigger} setting={digitalValues[0]}/>
          <PinCircle analog="NA" digital="D2" pwm="NA" x={465.08} y={334} trigger={trigger} setting={digitalValues[1]} />
          <PinCircle analog="NA" digital="D0" pwm="NA" x={465.08} y={524} trigger={trigger} setting={digitalValues[2]}/>
          <PinCircle analog="NA" digital="D1" pwm="NA" x={513.08} y={605} trigger={trigger} setting={digitalValues[3]}/>
          {/*Right Side */}
          <PinCircle analog="A10" digital="D10" pwm="D10" x={929.08} y={252} trigger={trigger} setting={digitalValues[4]}/>
          <PinCircle analog="A9" digital="D9" pwm="D9" x={975.08} y={334} trigger={trigger} setting={digitalValues[5]} />
          <PinCircle analog="A7" digital="D6" pwm="D6" x={975.08} y={520} trigger={trigger} setting={digitalValues[6]} />
          <PinCircle analog="A11" digital="D12" pwm="NA" x={929.08} y={602} trigger={trigger} setting={digitalValues[7]}/>

          {/* On Board Sensors*/}
          <HiddenPin x={790} y={275} width={50} height={70} trigger={() =>{trigger({Type: "TEMP", Analog: "A0", Digital: "NA", Pwm: "NA", Class: ".temp"})}} class_name=".temp" title={"Temperature Sensor"}/>
          <HiddenPin x={590} y={295} width={75} height={50} class_name=".light" title={"Light Sensor"} trigger={() => trigger({Type: "LIGHT", Analog: "A5", Digital: "NA", Pwm: "NA", Class: ".light"})}/>
          <HiddenPin x={605} y={535} width={60} height={45} class_name=".speaker" title={"Speaker"} trigger={() => trigger({Type: "TONE", Analog: "NA", Digital: "D5", Pwm: "NA",Class: ".speaker"})}/>
          <HiddenPin x={780} y={540} width={65} height={45} class_name=".sound" title={"Sound Sensor"} trigger={() => trigger({Type: "SOUND", Analog: "A4", Digital: "NA", Pwm: "NA", Class: ".sound"})}/>
          <HiddenPin x={705} y={420} width={65} height={55} class_name=".motion" title={"Motion Sensor"} trigger={() =>{trigger({Type: "MOTION", Analog: "NA", Digital: "NA", Pwm: "NA", Class: ".motion"})}} />

          {/* Neopixels */}
          <HiddenPin x={590} y={225} width={50} height={50} class_name=".border0" title={"NeoPixel #0"}  trigger={() =>{trigger({Type: "NEO", pixel: "0", Class: ".border0"})}}/> 
          <HiddenPin x={520} y={305} width={50} height={50} class_name=".border1" title={"NeoPixel #1"}  trigger={() =>{trigger({Type: "NEO", pixel: "1", Class: ".border1"})}}/> 
          <HiddenPin x={495} y={402.5} width={50} height={50} class_name=".border2" title={"NeoPixel #2"}  trigger={() =>{trigger({Type: "NEO", pixel: "2", Class: ".border2"})}}/> 
          <HiddenPin x={520} y={510} width={50} height={50} class_name=".border3" title={"NeoPixel #3"}  trigger={() =>{trigger({Type: "NEO", pixel: "3", Class: ".border3"})}}/>
          <HiddenPin x={600} y={585} width={50} height={40} class_name=".border4" title={"NeoPixel #4"}  trigger={() =>{trigger({Type: "NEO", pixel: "4", Class: ".border4"})}}/>

          <HiddenPin x={790} y={225} width={50} height={50} class_name=".border9" title={"NeoPixel #9"}  trigger={() =>{trigger({Type: "NEO", pixel: "9", Class: ".border9"})}}/> 
          <HiddenPin x={870} y={305} width={50} height={50} class_name=".border8" title={"NeoPixel #8"}  trigger={() =>{trigger({Type: "NEO", pixel: "8", Class: ".border8"})}}/> 
          <HiddenPin x={895} y={405} width={50} height={50} class_name=".border7" title={"NeoPixel #7"}  trigger={() =>{trigger({Type: "NEO", pixel: "7", Class: ".border7"})}}/> 
          <HiddenPin x={870} y={495} width={50} height={50} class_name=".border6" title={"NeoPixel #6"}  trigger={() =>{trigger({Type: "NEO", pixel: "6", Class: ".border6"})}}/>
          <HiddenPin x={800} y={585} width={50} height={40} class_name=".border5" title={"NeoPixel #5"}  trigger={() =>{trigger({Type: "NEO", pixel: "5", Class: ".border5"})}}/>
          <PinKey x={0} y={116} />  
      </>

  )
}

export default PlaygroundClassic_Layout