
import * as React from "react"


function PinSelector({id, analog, digital, pwm, x, y, trigger, setting=0, width, height, offset_h, offset_w, orientation="V"}) {
  let line_w = width/4;
  let line_h = offset_h;
  let text_orientation = "upright";
  let identifier ="pinSelector";
  if(orientation !== "V")
  {
    line_w = offset_w;
    line_h = height/4;
    text_orientation = "mixed";
  }
  
  let color = "black";
  if(setting === 1)
  {
    color= "url('#pinGradientHigh')";
  }
  else if(setting === 2)
  {
    color= "url('#pinGradientHighIn')";
  }
  else if(setting === 3)
  {
    color= "url('#pinGradientPwmOn')";
  }
  else if(setting === 4)
  {
    color= "url('#pinDisabled')";
  }
  else if(setting === 5)
  {
    color = "url('#cant_control')";
    identifier = "cantControl";
  }

  return(
      <>
      <rect x={x } y={y} fill={color} height={line_h} width={line_w}></rect>
      <g className={identifier} style={{cursor:'pointer', pointerEvents: "bounding-box"}} onClick={() => {trigger({Type: "PINS", Analog: analog, Digital: digital, Pwm: pwm})}}>
        <rect stroke={color} strokeWidth="2.5" id={digital + "_rect"} rx={width/10} ry={height/10} x={x + offset_w} y={y + offset_h} width={width} height={height} fill="white">
          <title>
            Digital: {digital.toString().substring(1) + "\n"}
            Analog: {analog === "NA" ? "Not Available \n" : analog + "\n"}
            Pwm: {pwm === "NA" ? "Not Available\n" : "Available\n"}
          </title> 
        </rect>
        <text id={digital + "_text"}  x={x + offset_w +  width/2} y={y + offset_h + 7.5} fill="black" style={{textOrientation: {text_orientation}, fontSize:String((width * 0.15) / 2)+ "em", fontWeight:"bold", writingMode:"vertical-rl"}}>{id}</text> 
      </g>
      </>
    )
}

export default PinSelector