import React, { useState} from 'react';
import Header_Expanded from './Header_Expanded.js';
import Header_Minimized from './Header_Minimized.js';
//import 'bootstrap';
/**
 * The Header component contains utilities for connecting the Live Debugger to a Serial Monitor and displaying instructions to the user.
 * This method accepts two methods (connect, disconnect) which handle calls to the Chrome Serial API for opening/closing port connections,
 * and a third boolean parameter which determines if a port connection has been successfully established
 * ```js
    <Header connect={connect} disconnect={disconnect} connectionStatus={eval(port !== "")}/>
    ```
 */

function Header({connect, disconnect, connectionStatus, selectBoard}) {
    let [headerExpanded, setHeaderExpanded] = useState(false);
    if(headerExpanded)
    {
        return <Header_Expanded connect={connect} disconnect={disconnect} connectionStatus = {connectionStatus} selectBoard={selectBoard}  expand = {setHeaderExpanded}/>;
    }
    else
    {
        return <Header_Minimized connect={connect} disconnect={disconnect} connectionStatus = {connectionStatus} selectBoard={selectBoard} expand = {setHeaderExpanded}/>;
    }
}
  
export default Header