
import * as React from "react"
/**
 * 
 * In order for the HiddenPin to color a sensor correctly,
 * the corresponding element on the board's svg file must contain the appropriate class_name in all elements that will have their colors changes
 * 
 * border_* are used for identifying the neo-pixel's which don't just change their fill color, but also their stroke-switch to show they have been selected
 */
function HiddenPin({x, y, height, width, class_name, title, trigger}) {
    function show(){
        const svgElements = document.querySelectorAll(class_name);
        if(class_name.includes("border"))
        {
          svgElements.forEach(element =>{
              element.setAttribute("stroke", "yellow");
              element.setAttribute("stroke-width", "0.65");
            }
          );
        }
        else{
          svgElements.forEach(element =>{
              element.setAttribute("fill", "yellow");
            }
          );
        }
        
    }

    function hide(){
        const svgElements = document.querySelectorAll(class_name);
        if(class_name.includes("border"))
        {
          svgElements.forEach(element =>{
              element.setAttribute("stroke", "white");
              element.setAttribute("stroke-width", "0.36");
            }
          );
        }
        else{
          svgElements.forEach(element =>{
              element.setAttribute("fill", "white");
            }
          );
        }
        
    }
  return(
      <>
        <rect  style={{cursor:'pointer'}} onMouseEnter={() => {show()}} onMouseLeave={() => {hide()}}  onClick={() => {trigger()}} x={x} y={y} width={width} height={height} fill="transparent">
          <title>
            {title}
          </title>  
        </rect>
          
      </>
    )
}

export default HiddenPin