import React, { useState} from 'react';
import instructable1 from "../../images/instructable1.png";
import instructable2 from "../../images/instructable2.png";

import connect_gif from "../../images/connect.gif";
import disconnect_gif from "../../images/disconnect.gif";
import control_summary_gif from "../../images/control_summary.gif";

import ColorTheme from "./ColorTheme.js";
//import 'bootstrap';
/**
 * The Header component contains utilities for connecting the Live Debugger to a Serial Monitor and displaying instructions to the user.
 * This method accepts two methods (connect, disconnect) which handle calls to the Chrome Serial API for opening/closing port connections,
 * and a third boolean parameter which determines if a port connection has been successfully established
 * ```js
    <Header connect={connect} disconnect={disconnect} connectionStatus={eval(port !== "")}/>
    ```
 */

function Header_Expanded({connect, disconnect, connectionStatus, selectBoard, expand}) {
    let height = 375; 

    let status = <div class="row color1" style={{width:"90%"}}>
                    <div class="col-2 pr-0">
                        <div class="spinner-border " role="status">
                            <span class="sr-only">No Connection</span>
                        </div>
                    </div>
                    <div class="col-10 pr-0 pt-1">
                        <p class="h4 text-left">No Connection</p>
                    </div>
                </div>;

    if(connectionStatus)
    {
        status =<div class="row color1" style={{width:"90%"}}>
                <div class="col-2 pr-0 pl-1">
                    <div class="spinner-grow" role="status">
                        <span class="sr-only">Device Connected</span>
                    </div>
                </div>
                <div class="col-10 pr-0 pl-1 pt-1">
                    <p class="h4 text-left">Device Connected</p>
                </div>
            </div>;
    }

    function hideOnHover(classShow, classHide)
    {
        document.getElementById(classHide).classList.add("collapse");
        document.getElementById(classShow).classList.remove("collapse");
    }


    return (
        <>    
          <div className="container-fluid p-0" style={{fontWeight:"bold"}}>
            {/*<textarea aria-label="Serial Monitor output" id="serial-textarea" value={serialText} readOnly/> Uncomment to view Serial Monitor messages*/}
            <div className="row no-gutters color1">
                {/* This area holds the connect/disconnect tools for establishing a port connection*/}
                <div className="col-12 col-md-2" style={{height: height, borderRight:"solid 3px var(--fontColor1)"}} >
                    <div class="row no-gutters justify-content-center mt-4" style={{height:"50%"}}>
                        <div class="col-5 m-2 pt-3 pb-1">
                        <button type="button" className="btn color1 btn-block" style={{height:"100%", fontWeight:"bold",fontSize:"1.25em"}} onClick={() => {connect()}} > Connect </button> 
                        </div>
                        <div class="col-5 m-2 pt-3 pb-1">
                        <button type="button" className="btn color1 btn-block" style={{ height:"100%", fontWeight:"bold", fontSize:"1.25em"}} onClick={() => {disconnect()}} disabled={!connectionStatus}> Disconnect </button> 
                        </div>
                    </div>
                    <div class="row no-gutters justify-content-center mt-4" style={{height:"15%"}}>
                        {status}
                    </div>
                    <div class="row no-gutters justify-content-center mt-2" style={{height:"15%"}}>
                        <div class="btn-group w-75" role="group" aria-label="Select Board Type" style={{marginTop:"1%"}}>
                            <button class="btn color1 dropdown-toggle" type="button" id="portBoard" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Select Board Type
                            </button>
                            <div class="dropdown-menu" id="portBoard_group" aria-labelledby="portBoard">
                                <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('PlaygroundClassic')}}>Adafruit Circuit Playground Classic</a>
                                <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{selectBoard('PlaygroundExpress')}}>Adafruit Circuit Playground Express</a>
                                <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{alert("Not supported Yet")}}>Arduino Uno</a>
                                <a class="dropdown-item" style={{cursor:"pointer"}}  onClick={() =>{alert("Not supported Yet")}}>BBC micro:bit</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-12 col-md-8 " style={{height: height, borderRight:"solid 3px var(--fontColor1)"}}>
                    <div class="row  justify-content-center mt-4 ml-1 mr-1" style={{height:"75%"}}>
                        {/* Overview Page's selectable/hover icons*/}
                        <div class="col-12 col-md-3 h-100 text-center " style={{cursor:"pointer"}} onClick={() => {alert("Click")}} 
                            onMouseEnter={() => {hideOnHover("overview_1B", "overview_1A")}}
                            onMouseLeave={() => {hideOnHover("overview_1A", "overview_1B")}}>
                            <div id="overview_1A" class="rounded-lg" style={{height: "95%", width: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <p style={{fontSize: "4em", margin:"30px auto 0 auto", fontWeight:"bolder"}}>1</p>
                                <p style={{fontSize: "3em", margin:"10px auto 0 auto"}}>Install <br/> Sketch</p>
                                
                            </div>
                            <div id="overview_1B" class="collapse rounded-lg" style={{height: "95%", width: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <svg aria-hidden="true" role="img" viewBox="0 0 16 16" width="40%" style={{margin: "30px auto 0px auto"}}><path fill="var(--fontColor1)" fill-rule="evenodd" d="M8.75 5V.75a.75.75 0 00-1.5 0V5H5.104a.25.25 0 00-.177.427l2.896 2.896a.25.25 0 00.354 0l2.896-2.896A.25.25 0 0010.896 5H8.75zM1.5 2.75a.25.25 0 01.25-.25h3a.75.75 0 000-1.5h-3A1.75 1.75 0 000 2.75v7.5C0 11.216.784 12 1.75 12h3.727c-.1 1.041-.52 1.872-1.292 2.757A.75.75 0 004.75 16h6.5a.75.75 0 00.565-1.243c-.772-.885-1.193-1.716-1.292-2.757h3.727A1.75 1.75 0 0016 10.25v-7.5A1.75 1.75 0 0014.25 1h-3a.75.75 0 000 1.5h3a.25.25 0 01.25.25v7.5a.25.25 0 01-.25.25H1.75a.25.25 0 01-.25-.25v-7.5zM9.018 12H6.982a5.72 5.72 0 01-.765 2.5h3.566a5.72 5.72 0 01-.765-2.5z"></path></svg>
                                <p class="h6 text-justify mt-1 p-3">
                                    Download, compile, and install LiveDebugger.ino onto your device.
                                </p>
                            </div>
                        </div>
                        <div class="col-12 col-md-3 h-100 text-center" style={{cursor:"pointer"}} onClick={() => {alert("Click")}} 
                            onMouseEnter={() => {hideOnHover("overview_2B", "overview_2A")}}
                            onMouseLeave={() => {hideOnHover("overview_2A", "overview_2B")}}>
                            <div id="overview_2A" class="rounded-lg" style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <p style={{fontSize: "4em", margin:"30px auto 0 auto", fontWeight:"bolder"}}>2</p>
                                <p style={{fontSize: "3em", margin:"10px auto 0 auto"}}>Connect Device</p>                                                    
                            </div>
                            <div id="overview_2B" class="collapse rounded-lg" style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <svg viewBox="0 0 512 512" width="40%" style={{margin: "30px auto 0px auto"}}>
                                    <path fill="var(--fontColor1)" d="M358.195 1.324L262.39 97.125l-13.738-13.738c-1.878-1.879-4.773-1.883-6.656 0L87.703 237.68c-28.344 28.343-30.656 72.886-6.504 104.117l-34.562 34.562 9.902 9.906L.234 442.57l-.121.121-.113.114 24.488 24.488.055.055.113.113 63.965-63.965.024-.023 19.886 19.882-63.738 63.738-.137.137-.117.114 24.652 24.652 56.543-56.539 9.906 9.91 34.492-34.492c30.946 24.148 75.699 21.906 104.187-6.582L428.614 270c1.878-1.882 1.882-4.77.074-6.578l-13.812-13.808 95.805-95.805c1.804-1.805 1.738-4.77-.07-6.578L364.774 1.394c-1.809-1.808-4.774-1.874-6.579-.07zM135.566 415.981l-39.547-39.547 14.657-14.656 39.55 39.546-14.66 14.657zM268 293.551l-2.172-2.176-10.445 10.446-28.966-8.481-13.859 13.855c2.898 6 1.867 13.242-3.098 18.207-6.312 6.313-16.656 6.313-22.973-.004-6.305-6.305-6.199-16.543.114-22.855 5.172-5.172 13.129-6.102 19.238-2.691l4.55-4.555-8.59-29.054 12.722-12.726c-.515-2.79.313-5.898 2.586-8.168 3.515-3.516 9.414-3.626 13.031-.004 3.625 3.621 3.515 9.515 0 13.031-1.258 1.258-2.813 2.058-4.446 2.438-.102.023-.203.039-.301.058-1.629.316-3.32.226-4.867-.324l-9.106 9.106 3.766 12.691 1.711 6.019.058-.058.051.172 12.722-12.516 26.309-26.308.054-.059.114-.109-4.637-4.637-2.11-2.219 9.332-2.558 12.668-3.344 6.133-1.539-2.457 9.223-3.402 12.707-1.734 6.16-6.758-6.762-20.527 20.527-9.52 9.418.078.024-.129.125 5.996 1.867 12.824 3.722 7.215-7.214.015-.016.114-.114-1.965-1.965 10.55-10.55 10.66 10.656L268 293.551zm119.043-65.414l-103.18-103.18 77.578-77.578L464.62 150.558l-77.577 77.579z"  />
                                    <path fill="var(--fontColor1)" d="M349.313 93.828h-.004l-.114-.117L330.906 112l21.18 21.184 18.289-18.29-21.02-21.023zM400 181.093l18.289-18.288-21.179-21.18-18.137 18.136-.043.04h.004l-.114.113z"/>
                                </svg>
                                <p class="h6 text-justify mt-1 p-3">
                                    Click "Connect" and allow the Live Debugger to access your device's Serial Port.
                                </p>
                            </div>
                        </div>
                        <div class="col-12 col-md-3 h-100 text-center"  style={{cursor:"pointer"}} onClick={() => {alert("Click")}} 
                            onMouseEnter={() => {hideOnHover("overview_3B", "overview_3A")}}
                            onMouseLeave={() => {hideOnHover("overview_3A", "overview_3B")}}>
                            <div id="overview_3A" class=" rounded-lg" style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <p style={{fontSize: "4em", margin:"30px auto 0 auto", fontWeight:"bolder"}}>3</p>
                                <p style={{fontSize: "3em", margin:"10px auto 0 auto"}}>Control Device</p>                                                      
                            </div>
                            <div id="overview_3B" class="collapse rounded-lg" style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1372.201 1372.684" width="40%" style={{margin: "30px auto 0px auto"}} >
                                    <path opacity="1" fill="var(--fontColor1)" stroke="var(--fontColor1)" stroke-miterlimit="10" d="M1371.701 686.595c0 378.65-306.972 685.606-685.549 685.606C307.451 1372.201.5 1065.23.5 686.595.5 308.019 307.451 1.048 686.152 1.048c378.594.016 685.549 306.97 685.549 685.547z"/>
                                    <path fill="var(--bgColor1)" d="M947.959 931.196c-12.909 0-26.127-.929-39.127-2.864-108.978-15.554-181.848-93.822-222.665-153.989-40.946 60.166-113.811 138.512-222.74 154.045a275.864 275.864 0 0 1-39.133 2.785c-67.753 0-131.358-25.217-179.201-71.003-48.299-46.165-74.951-108.114-74.951-174.171 0-66.14 26.651-128.004 75.021-174.253 47.797-45.793 111.449-70.936 179.231-70.936 12.918 0 26.067.928 39.023 2.783 108.932 15.535 181.794 93.813 222.743 153.99 40.825-60.177 113.689-138.432 222.658-153.99 13-1.863 26.148-2.783 39.066-2.783 67.753 0 131.401 25.208 179.197 70.936 48.345 46.249 74.937 108.113 74.937 174.253 0 66.057-26.524 128.006-74.868 174.171-47.881 45.785-111.434 71.026-179.191 71.026M734.42 686.024c21.283 40.534 84.067 141.676 186.692 156.375 8.984 1.236 18.028 1.923 26.839 1.923 92.185 0 167.225-71.002 167.225-158.322s-75.023-158.321-167.291-158.321c-8.812 0-17.853.629-26.753 1.921-102.644 14.664-165.428 115.806-186.712 156.424M424.393 527.702c-92.308 0-167.36 70.998-167.36 158.321 0 87.305 75.021 158.322 167.245 158.322 8.852 0 17.897-.688 26.879-1.922 102.629-14.697 165.394-115.783 186.689-156.375-21.237-40.535-84.061-141.761-186.689-156.376-8.877-1.341-17.945-1.97-26.764-1.97"/>
                                    <path fill="var(--bgColor1)" d="M354.37 662.051h152.625v49.181H354.37zM1016.484 662.051h-51.671v-51.747h-49.348v51.747h-51.648v49.181h51.648v51.737h49.348v-51.737h51.671z"/>
                                </svg>
                                <p class="h6 text-justify mt-1 p-3">
                                    Use the Live Debugger to control your device's hardware pins and on-board sensors.
                                </p>
                            </div>
                        </div>
                        <div class="col-12 col-md-3 h-100 text-center"  style={{cursor:"pointer"}} onClick={() => {alert("Click")}} 
                            onMouseEnter={() => {hideOnHover("overview_4B", "overview_4A")}}
                            onMouseLeave={() => {hideOnHover("overview_4A", "overview_4B")}}>
                            <div id="overview_4A" class="rounded-lg " style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <p style={{fontSize: "4em", margin:"30px auto 0 auto", fontWeight:"bolder"}}>4</p>
                                <p style={{fontSize: "3em", margin:"10px auto 0 auto"}}>Repo &amp; <br/> Tutorials</p> 
                            </div>
                            <div id="overview_4B" class="collapse rounded-lg " style={{height: "95%", border: "solid 3px var(--fontColor1)"}}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"  width="40%" style={{margin: "30px auto 0px auto"}}><path id="gitLogo" fill="var(--fontColor1)" fill-rule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0016 8c0-4.42-3.58-8-8-8z"></path></svg> 
                                <p class="h6 text-justify mt-1 p-3">
                                    Follow detailed tutorials for using the Live Debugger and extending its features.
                                </p>
                            </div>
                        </div>
                    </div> 
                    <div class="row no-gutters justify-content-center mt-4 pt-2" style={{height:"18%", borderTop:"solid 3px var(--fontColor1)"}}>
                        <a onClick={() => expand(false)} style={{cursor:"pointer"}}>
                            <div class="row no-gutters justify-content-center">
                                <div class="col-2">
                                    <svg xmlns="http://www.w3.org/2000/svg" class="float-right" viewBox="0 0 16 16" width="18%" ><path fill="var(--fontColor1)" fill-rule="evenodd" d="M3.22 9.78a.75.75 0 010-1.06l4.25-4.25a.75.75 0 011.06 0l4.25 4.25a.75.75 0 01-1.06 1.06L8 6.06 4.28 9.78a.75.75 0 01-1.06 0z"></path></svg>
                                </div>
                                <div class="col-6">
                                    <p class="h3 text-center">Collapse to Hide Instructions</p>
                                </div>
                                <div class="col-2">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" width="18%" ><path fill="var(--fontColor1)" fill-rule="evenodd" d="M3.22 9.78a.75.75 0 010-1.06l4.25-4.25a.75.75 0 011.06 0l4.25 4.25a.75.75 0 01-1.06 1.06L8 6.06 4.28 9.78a.75.75 0 01-1.06 0z"></path></svg>
                                </div>
                            </div>
                        </a>
                    </div>
                </div>
                <div class="col-12 col-md-2 color1" style={{height: height}}>
                    <div class="row no-gutters justify-content-center h-75" >
                        <div class="card-body pb-0 pt-2 text-center"> 
                            <p class="h1 text-center ">Live <br/> Debugger</p>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15 16" style={{width:"70%"}}><path fill="var(--fontColor1)" fill-rule="evenodd" d="M11 10h3V9h-3V8l3.17-1.03-.34-.94L11 7V6c0-.55-.45-1-1-1V4c0-.48-.36-.88-.83-.97L10.2 2H12V1H9.8l-2 2h-.59L5.2 1H3v1h1.8l1.03 1.03C5.36 3.12 5 3.51 5 4v1c-.55 0-1 .45-1 1v1l-2.83-.97-.34.94L4 8v1H1v1h3v1L.83 12.03l.34.94L4 12v1c0 .55.45 1 1 1h1l1-1V6h1v7l1 1h1c.55 0 1-.45 1-1v-1l2.83.97.34-.94L11 11v-1zM9 5H6V4h3v1z"></path></svg>
                        </div>
                    </div>
                    <div class="row no-gutters justify-content-center mt-4" style={{height:"15%"}}>
                        <ColorTheme width="75%" />
                    </div>
                </div>
            </div>
        </div>
       </>
  
      );
  }
  
  export default Header_Expanded