
import * as React from "react";
import PinCircle from "../PinControls/PinCircle.js";
import PlaygroundExpress from "./PlaygroundExpress.js";
import PinKey from "../PinControls/PinKey.js"
import HiddenPin from "../PinControls/HiddenPin.js"

function PlaygroundExpress_Layout({pinData, trigger, disabled, deviceData}) {
  let digitalPins = ["D3", "D2", "D0", "D1", "D10", "D9", "D6", "D12"];
  let digitalValues = [0,0,0,0,0,0,0,0];
  
  //Preset Digital Interfaces
  let buttonRight =  <stop offset="0%" stop-color="#1E1E1E"></stop>;
  let buttonLeft =  <stop offset="0%" stop-color="#1E1E1E"></stop>;
  let bottomSwitch =  <stop offset="0%" stop-color="#333333"></stop>;


    //Handle onboard sensor data
  let temp = "NA";
  if(pinData !== null)
  {
    try{
        if(disabled)
        {
          digitalValues = [4,4,4,4,4,4,4,4];
        }
        else
        {
          if(pinData.hasOwnProperty("A9"))
          {
            temp = pinData["A9"];
          }

          if(pinData["D4"][0] === 2)
          {
            buttonLeft =  <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                          </stop>;
          }

          if(pinData["D5"][0] === 2)
          {
            buttonRight = <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                        </stop>;
          }

          if(pinData["D7"][0] === 2)
          {
            bottomSwitch = <stop offset="0%" stop-color="yellow">
                            <animate attributeName="stop-color" values="white;yellow;white" dur="1s" repeatCount="indefinite"></animate>
                        </stop>;
          }
          
          for(let i = 0; i < digitalPins.length; i++)
          {
            digitalValues[i] = pinData[digitalPins[i]][0];
          }
          
          if(deviceData.pwm["D0"] > 0 && deviceData.pwm["D0"] < 255)
          {
            digitalValues[2] = 3;
          }
          if(deviceData.pwm["D1"] > 0 && deviceData.pwm["D1"] < 255)
          {
            digitalValues[3] = 3;
          }
          if(deviceData.pwm["D6"] > 0 && deviceData.pwm["D6"] < 255)
          {
            digitalValues[6] = 3;
          }
          if(deviceData.pwm["D9"] > 0 && deviceData.pwm["D9"] < 255)
          {
            digitalValues[5] = 3;
          }
          if(deviceData.pwm["D10"] > 0 && deviceData.pwm["D10"] < 255)
          {
            digitalValues[4] = 3;
          }
        }
      }
    catch(e)
    {
      console.log(e);
    }
  }


  return (
      <>
        <PlaygroundExpress width={600} height={600} x={420.08} y={125} />
        {/* Animations for the pin's current state (Low, High, High-In, PWM-ON, Disabled*/}
        <defs>  
            <linearGradient id="pinGradientHigh" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="red">
                    <animate attributeName="stop-color" values="white;red;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinGradientHighIn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="yellow">
                    <animate attributeName="stop-color" values="white;yellow;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinGradientPwmOn" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="#66CCEE">
                    <animate attributeName="stop-color" values="white;#66CCEE;white" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="pinDisabled" x1="0%" y1="0%" x2="100%" y2="100%" > 
                <stop offset="0%" stopColor="black">
                    <animate attributeName="stop-color" values="black" dur="1.5s" repeatCount="indefinite"></animate>
                </stop>
            </linearGradient>
            <linearGradient id="buttonRight" x1="0%" y1="0%" x2="100%" y2="100%" > 
               {buttonRight} 
            </linearGradient>
            <linearGradient id="buttonLeft" x1="0%" y1="0%" x2="100%" y2="100%" > 
                {buttonLeft}
            </linearGradient>
            <linearGradient id="bottomSwitch" x1="0%" y1="0%" x2="100%" y2="100%" > 
                {bottomSwitch}
            </linearGradient>
          </defs>
        
        
        {/*Left Side */}
        <PinCircle analog="A4" digital="D3" pwm="NA" x={511.08} y={248} trigger={trigger} setting={digitalValues[0]}/>
        <PinCircle analog="A5" digital="D2" pwm="NA" x={465.08} y={330} trigger={trigger} setting={digitalValues[1]} />
        <PinCircle analog="A6" digital="D0" pwm="D0" x={465.08} y={520} trigger={trigger} setting={digitalValues[2]}/>
        <PinCircle analog="A7" digital="D1" pwm="D1" x={513.08} y={601} trigger={trigger} setting={digitalValues[3]}/>

        {/*Right Side */}
        <PinCircle analog="A3" digital="D10" pwm="D10" x={929.08} y={248} trigger={trigger} setting={digitalValues[4]}/>
        <PinCircle analog="A2" digital="D9" pwm="D9" x={975.08} y={330} trigger={trigger} setting={digitalValues[5]} />
        <PinCircle analog="A1" digital="D6" pwm="D6" x={976.08} y={518} trigger={trigger} setting={digitalValues[6]} />
        <PinCircle analog="A0" digital="D12" pwm="NA" x={929.08} y={602} trigger={trigger} setting={digitalValues[7]}/>
        
        {/* On Board Sensors*/}
        <HiddenPin x={795} y={280} width={55} height={70} trigger={() =>{trigger({Type: "TEMP", Analog: "A9", Digital: "NA", Pwm: "NA", Class: ".temp"})}} class_name=".temp" title={"Temperature Sensor"}/>
        <HiddenPin x={595} y={295} width={65} height={60} class_name=".light" title={"Light Sensor"} trigger={() => trigger({Type: "LIGHT", Analog: "A8", Digital: "NA", Pwm: "NA", Class: ".light"})}/>
        <HiddenPin x={610} y={535} width={60} height={45} class_name=".speaker" title={"Speaker"} trigger={() => trigger({Type: "TONE", Analog: "A0", Digital: "NA", Pwm: "NA",Class: ".speaker"})}/>
        <HiddenPin x={825} y={515} width={35} height={45} class_name=".sound" title={"Sound Sensor"} trigger={() => trigger({Type: "SOUND", Analog: "A4", Digital: "NA", Pwm: "NA", Class: ".sound"})}/>
        <HiddenPin x={670} y={426} width={65} height={58} class_name=".motion" title={"Motion Sensor"} trigger={() =>{trigger({Type: "MOTION", Analog: "A9", Digital: "NA", Pwm: "NA", Class: ".motion"})}} /> 

        {/* Neopixels */}
        <HiddenPin x={590} y={220} width={55} height={55} class_name=".border0" title={"NeoPixel #0"}  trigger={() =>{trigger({Type: "NEO", pixel: "0", Class: ".border0"})}}/> 
        <HiddenPin x={520} y={295} width={50} height={55} class_name=".border1" title={"NeoPixel #1"}  trigger={() =>{trigger({Type: "NEO", pixel: "1", Class: ".border1"})}}/> 
        <HiddenPin x={492.5} y={400} width={50} height={50} class_name=".border2" title={"NeoPixel #2"}  trigger={() =>{trigger({Type: "NEO", pixel: "2", Class: ".border2"})}}/> 
        <HiddenPin x={520} y={500} width={50} height={55} class_name=".border3" title={"NeoPixel #3"}  trigger={() =>{trigger({Type: "NEO", pixel: "3", Class: ".border3"})}}/>
        <HiddenPin x={600} y={585} width={50} height={45} class_name=".border4" title={"NeoPixel #4"}  trigger={() =>{trigger({Type: "NEO", pixel: "4", Class: ".border4"})}}/>

        <HiddenPin x={795} y={220} width={50} height={50} class_name=".border9" title={"NeoPixel #9"}  trigger={() =>{trigger({Type: "NEO", pixel: "9", Class: ".border9"})}}/> 
        <HiddenPin x={870} y={305} width={50} height={50} class_name=".border8" title={"NeoPixel #8"}  trigger={() =>{trigger({Type: "NEO", pixel: "8", Class: ".border8"})}}/> 
        <HiddenPin x={897} y={400} width={52} height={52} class_name=".border7" title={"NeoPixel #7"}  trigger={() =>{trigger({Type: "NEO", pixel: "7", Class: ".border7"})}}/> 
        <HiddenPin x={870} y={495} width={52} height={52} class_name=".border6" title={"NeoPixel #6"}  trigger={() =>{trigger({Type: "NEO", pixel: "6", Class: ".border6"})}}/>
        <HiddenPin x={800} y={575} width={50} height={50} class_name=".border5" title={"NeoPixel #5"}  trigger={() =>{trigger({Type: "NEO", pixel: "5", Class: ".border5"})}}/>
        <PinKey x={0} y={115} />  
     </>
  );
}

export default PlaygroundExpress_Layout